
import progress from '../common/progress'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Masker from 'vux/src/components/masker/index.vue'

export default {
components: {
ViewBox,
XHeader,
Masker,
'rate': progress
},
data () {
return {
index: 0,
tip: ''
}
},
computed: {
state () {
return this.$store.state.applyState
}
},
mounted () {
this.getWrongInfo()
},
methods: {
getWrongInfo () {

let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/applyPageInit.do'
let data = {
action: 'init_request',
path: path,
params: {
token: this.$store.state.userInfo.token

}
}

self.$store.dispatch('normalRequest', data).then(data => {

if (data.response === 'success') {
if (data.data.explain === '6') {
self.tip = '未签约（签约超时）'
} else if (data.data.explain === '2') {
self.tip = '授信审批未通过'
}
} else {
this.$vux.toast.text(data.data)
}
})
}
}
}



